'use client'

import Link from 'next/link'
import { useState, useEffect } from 'react'
import { Menu, X, ChevronDown } from 'lucide-react'

export function Navigation() {
  const [isOpen, setIsOpen] = useState(false)
  const [scrolled, setScrolled] = useState(false)

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 50)
    }
    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  return (
    <nav className={`fixed top-0 z-50 w-full transition-all duration-300 ${
      scrolled ? 'bg-background/95 backdrop-blur border-b border-border shadow-md' : 'bg-transparent'
    }`}>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-24">
          <Link href="/" className="flex-shrink-0 group">
            <div className="text-left">
              <div className="text-2xl font-serif font-bold tracking-wide text-primary group-hover:scale-105 transition-transform duration-300">
                MADDIE TAVARES
              </div>
              <div className="text-sm font-light italic text-primary/80 tracking-widest -mt-1">
                beauty boutique
              </div>
            </div>
          </Link>

          {/* Desktop Menu */}
          <div className="hidden md:flex gap-8 items-center">
            <Link href="/" className="text-sm luxury-text hover:text-primary transition duration-300">Home</Link>
            <Link href="/servicos" className="text-sm luxury-text hover:text-primary transition duration-300">Serviços</Link>
            <Link href="/sobre" className="text-sm luxury-text hover:text-primary transition duration-300">Sobre</Link>
            <Link href="/contato" className="text-sm luxury-text hover:text-primary transition duration-300">Contato</Link>
            <Link href="/agendar" className="luxury-button bg-primary text-primary-foreground hover:bg-primary/90">
              Agendar
            </Link>
            <Link href="/login" className="text-sm luxury-text hover:text-primary transition duration-300">Entrar</Link>
          </div>

          {/* Mobile Menu Button */}
          <button 
            className="md:hidden text-foreground"
            onClick={() => setIsOpen(!isOpen)}
          >
            {isOpen ? <X size={28} /> : <Menu size={28} />}
          </button>
        </div>

        {/* Mobile Menu */}
        {isOpen && (
          <div className="md:hidden pb-6 space-y-4 bg-background/95 backdrop-blur border-b border-border">
            <Link href="/" className="block text-sm luxury-text hover:text-primary py-2">Home</Link>
            <Link href="/servicos" className="block text-sm luxury-text hover:text-primary py-2">Serviços</Link>
            <Link href="/sobre" className="block text-sm luxury-text hover:text-primary py-2">Sobre</Link>
            <Link href="/contato" className="block text-sm luxury-text hover:text-primary py-2">Contato</Link>
            <Link href="/agendar" className="block w-full px-4 py-2 bg-primary text-primary-foreground rounded-full text-sm font-medium hover:bg-primary/90 transition text-center">
              Agendar
            </Link>
            <Link href="/login" className="block text-sm luxury-text hover:text-primary py-2">Entrar</Link>
          </div>
        )}
      </div>
    </nav>
  )
}
